  background-color: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  align-items: center;
  width: 200px;
  height: 408px;
  padding: 32px 16px 16px;
}

.card__image {
  box-sizing: border-box;
  width: 160px;
  height: 134px;
  margin: 0 auto;
  display: block;
}

.card__title {
  margin-top: 40px;
  margin-bottom: 0;
  font-size: 12px;
  font-weight: 500;
}

.card__code {
  text-align: left;
  color: #616070;
  margin-top: 4px;
  line-height: 14px;
}

.card__price {
  justify-content: space-between;
  margin-top: 24px;
  display: flex;
}

.card__text {
  color: #616070;
  font-size: 12px;
}

.card__number {
